import Moment from 'moment';
import { extendMoment } from 'moment-range';

const moment = extendMoment(Moment);
import { useState } from 'react';
import { MonthView } from './MonthView';

export const CalendarView = ({ year, month, onCellClick }) => {
  const [currentYear, setCurrentYear] = useState(year);
  const [allMonths, setAllMonths] = useState(moment.months());
  const [currentMonth, setCurrentMonth] = useState(allMonths[month - 1]);

  const handleMonthChange = newMonth => {
    setCurrentMonth(newMonth);
  };

  return (
    <div className="Container">
      <div className="row">
        <h3 className="text-center">{currentYear}</h3>
      </div>
      <div className="row">
        <MonthView onCellClick={onCellClick} month={currentMonth} year={year}></MonthView>
      </div>
      <div className="row">
        <ul className="list-group list-group-horizontal">
          {allMonths.map(m => {
            return (
              <li
                key={m}
                className={m == currentMonth ? 'list-group-item active' : 'list-group-item'}
                onClick={e => handleMonthChange(e.target.dataset.value)}
                data-value={m}
              >
                {m}
              </li>
            );
          })}
        </ul>
      </div>
    </div>
  );
};
