import './styles.module.css';

import Moment from 'moment';
import { extendMoment } from 'moment-range';

const moment = extendMoment(Moment);
import { useEffect, useState } from 'react';

export const MonthView = ({ year, month, onCellClick }) => {
  const [startDate, setStartDate] = useState(moment(year.concat('-').concat(month)));
  const [firstDay, setFirstDay] = useState(moment(year.concat('-').concat(month)).startOf('month'));
  const [endDay, setEndDay] = useState(moment(year.concat('-').concat(month)).endOf('moth'));
  const [monthRange, setMonthRange] = useState(
    Array.from(
      moment
        .range(
          moment(year.concat('-').concat(month)).startOf('month').toDate(),
          moment(year.concat('-').concat(month)).endOf('month').toDate(),
        )
        .by('days'),
    ),
  );
  const [weeks, setWeeks] = useState([]);

  useEffect(() => {
    const startDate = moment(year.concat('-').concat(month));
    setFirstDay(startDate.startOf('month'));
    setEndDay(startDate.endOf('month'));
    const newMonthRange = Array.from(
      moment
        .range(startDate.startOf('month').toDate(), startDate.endOf('month').toDate())
        .by('days'),
    );
    const newWeeks = [];
    newMonthRange.map(d => {
      console.log(d.week());
      if (!newWeeks.includes(d.week())) {
        newWeeks.push(d.week());
      }
    });
    setWeeks(newWeeks);
    setMonthRange(newMonthRange);
  }, [month, year]);

  return (
    <div className="table-responsive">
      <table className="table table-bordered">
        <thead>
          <tr>
            <th scope="col">Week</th>
            {moment.weekdays().map(weekDay => (
              <th scope="col" key={weekDay} className="days-of-week">
                {weekDay}
              </th>
            ))}
          </tr>
        </thead>
        <tbody>
          {weeks.map(week => {
            return (
              <tr key={week}>
                <th scope="row">{week}</th>
                {monthRange.filter(d => d.format('W') == week).map(<></>)}
              </tr>
            );
          })}
          {/* {weeksRange.map(week => {
            var daysRange = Array.from(
              moment.range(week.startOf('week').toDate(), week.endOf('week').toDate()).by('day'),
            );

            return (
              <tr>
                {daysRange.map(d => (
                  <td
                    data-value={d.format()}
                    className={d.format('MM') != month ? 'text-secondary' : 'text-body'}
                    onClick={e => onCellClick(e.target.dataset.value)}
                  >
                    {d.format('DD')}
                  </td>
                ))}
              </tr>
            );
          })} */}
        </tbody>
      </table>
    </div>
  );
};
